"use client"

import { useState, useEffect } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Plus, Trash2, Upload, ImageIcon } from "lucide-react"

interface Photo {
  id: number
  src: string
  title: string
  category: string
}

const initialPhotos: Photo[] = [
  { id: 1, src: "/students-learning-computer-class.jpg", title: "Computer Class", category: "classes" },
  { id: 2, src: "/diwali-celebration-decoration-lights.jpg", title: "Diwali Celebration", category: "celebration" },
  { id: 3, src: "/independence-day-flag-hoisting-ceremony.jpg", title: "Independence Day", category: "celebration" },
  { id: 4, src: "/certificate-distribution-ceremony-graduation.jpg", title: "Certificate Distribution", category: "events" },
  { id: 5, src: "/teachers-day-celebration-students-flowers.jpg", title: "Teachers Day", category: "celebration" },
  { id: 6, src: "/annual-function-school-stage-performance.jpg", title: "Annual Function", category: "events" },
]

export function GalleryManager() {
  const [photos, setPhotos] = useState<Photo[]>(initialPhotos)
  const [newPhoto, setNewPhoto] = useState({ title: "", category: "", file: null as File | null })
  const [photoPreview, setPhotoPreview] = useState<string | null>(null)
  const [dialogOpen, setDialogOpen] = useState(false)

  useEffect(() => {
    // Load photos from localStorage
    const savedPhotos = localStorage.getItem("galleryPhotos")
    if (savedPhotos) {
      setPhotos(JSON.parse(savedPhotos))
    } else {
      localStorage.setItem("galleryPhotos", JSON.stringify(initialPhotos))
    }
  }, [])

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      if (file.size > 5 * 1024 * 1024) { // 5MB limit
        alert("File size must be less than 5MB")
        return
      }
      
      setNewPhoto({ ...newPhoto, file })
      
      // Create preview
      const reader = new FileReader()
      reader.onloadend = () => {
        setPhotoPreview(reader.result as string)
      }
      reader.readAsDataURL(file)
    }
  }

  const handleAddPhoto = () => {
    if (newPhoto.title && newPhoto.category && photoPreview) {
      const updatedPhotos = [
        ...photos,
        {
          id: Date.now(),
          src: photoPreview, // In production, upload to server and use URL
          title: newPhoto.title,
          category: newPhoto.category,
        },
      ]
      
      setPhotos(updatedPhotos)
      localStorage.setItem("galleryPhotos", JSON.stringify(updatedPhotos))
      
      setNewPhoto({ title: "", category: "", file: null })
      setPhotoPreview(null)
      setDialogOpen(false)
    }
  }

  const handleDeletePhoto = (id: number) => {
    const updatedPhotos = photos.filter((p) => p.id !== id)
    setPhotos(updatedPhotos)
    localStorage.setItem("galleryPhotos", JSON.stringify(updatedPhotos))
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-foreground">Gallery Manager</h2>
          <p className="text-muted-foreground">Upload and manage celebration photos</p>
        </div>
        <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
          <DialogTrigger asChild>
            <Button>
              <Plus className="mr-2 h-4 w-4" /> Add Photo
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Add New Photo</DialogTitle>
            </DialogHeader>
            <div className="space-y-4 py-4">
              <div className="space-y-2">
                <Label>Photo Title</Label>
                <Input
                  value={newPhoto.title}
                  onChange={(e) => setNewPhoto({ ...newPhoto, title: e.target.value })}
                  placeholder="Enter photo title"
                />
              </div>
              <div className="space-y-2">
                <Label>Category</Label>
                <Select
                  value={newPhoto.category}
                  onValueChange={(value) => setNewPhoto({ ...newPhoto, category: value })}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select category" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="celebration">Celebration</SelectItem>
                    <SelectItem value="events">Events</SelectItem>
                    <SelectItem value="classes">Classes</SelectItem>
                    <SelectItem value="other">Other</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="space-y-2">
                <Label>Upload Image</Label>
                <div className="border-2 border-dashed border-border rounded-lg p-6 text-center">
                  {photoPreview ? (
                    <div className="space-y-2">
                      <img
                        src={photoPreview}
                        alt="Preview"
                        className="max-h-48 mx-auto rounded-lg object-cover"
                      />
                      <p className="text-sm text-muted-foreground">Image ready to upload</p>
                    </div>
                  ) : (
                    <>
                      <ImageIcon className="mx-auto h-8 w-8 text-muted-foreground" />
                      <p className="mt-2 text-sm text-muted-foreground">Select an image file (Max 5MB)</p>
                    </>
                  )}
                  <Input
                    type="file"
                    accept="image/*"
                    onChange={handleFileChange}
                    className="mt-2"
                  />
                </div>
              </div>
              <Button
                onClick={handleAddPhoto}
                className="w-full"
                disabled={!newPhoto.title || !newPhoto.category || !photoPreview}
              >
                Add to Gallery
              </Button>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
        {photos.map((photo) => (
          <Card key={photo.id} className="overflow-hidden">
            <div className="relative aspect-video">
              <img src={photo.src || "/placeholder.svg"} alt={photo.title} className="w-full h-full object-cover" />
              <Button
                variant="destructive"
                size="icon"
                className="absolute top-2 right-2"
                onClick={() => handleDeletePhoto(photo.id)}
              >
                <Trash2 className="h-4 w-4" />
              </Button>
            </div>
            <CardContent className="p-4">
              <h3 className="font-medium text-foreground">{photo.title}</h3>
              <span className="inline-block mt-1 px-2 py-0.5 rounded-full bg-muted text-xs text-muted-foreground capitalize">
                {photo.category}
              </span>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  )
}
